@import "./variables.scss";
@import "./components/mixins.scss";
@import "./components/base.scss";
@import "./components/buttons.scss";
@import "./components/animations.scss";
@import "./components/nav.scss";
@import "./components/footer.scss";

* {
    font-family: $text-sans;
}

body {
    background-color: $bg-color;
  }

#navbar {
background-color: rgba(249,249,249,80%);
position: fixed;
top: 0;
left: 0;
width: 100%;
opacity: 1;
transition: opacity 0.3s ease-in-out;
}

#navbar.show {
    opacity: 1;
    transition: opacity 0.3s ease-in-out;
}

#navbar.hidden {
    opacity: 0;
  }

#content {
    margin-top: 80px;
}

H1 {
    font: {
        size: 40px;
        weight: 400;
    }
}

H2 {
    font: {
        size: 24px;
        weight: 300;
    }
    color: #6a6a6a;
}

H3 {
    font: {
        size: 22px;
        weight: 400;
    }
    color: $text-color;
}

H4 {
    font: {
        size: 18px;
        weight: 600;
    }
    color: $text-color;
}

.h4-handwrite {
    font: {
        size: 20px;
        family: "Mynerve", sans-serif;
    }
}

.handwriting {
    font-family: $text-hand;
}

.green {
    color: $primary-color;
}

.white {
    color: #ffffff;
}

.bg-transparent {
    background-color: rgba(0,0,0,0%) !important;
}

.work-tag {
    color: $primary-color;
}

.underline {
    text-decoration: underline;
    text-underline-offset: 6px;
}

.fourpx {
    margin-bottom: 4px;
}

/* Project Layout */

.project-intro {
    display: flex;
    justify-content: center;
    align-items: start;
    padding: 80px 0;
    gap: 5%;
    color: #ffffff;
}

.intro-title {
    font-weight: 300;
}

.intro-content {
    font-weight: 600;
    line-height: 1.5;
}

.middle {
    display: flex;
    justify-content: center;
}

.padding-end {
    padding-bottom: 36px;
}

.project-text {
    color: $text-color;
    width: 720px;
    line-height: 1.5;
    font-size: 1.125rem;
    img {
        width: 720px;
        height: auto;
        padding: 24px 0;
    }
    h2 {
        font-size: 1.625rem;
        padding: 64px 0 20px 0;
        font-weight: 600;
    }
    h4 {
        font-weight: 400;
        padding: 24px 0 12px 0;
    }
}

.project-3grid {
    width: 840px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 28px;
    padding: 36px 0 36px 0;
}

.threegrid-box {
    padding: 32px;
    border-radius: 16px;
    p {
        color: $text-color;
        font-weight: 400;
        line-height: 1.5;
    }
}

.icon-title {
    display: flex;
    align-items: center;
    padding-bottom: 12px;
    img {
        padding-right: 8px;
    }
    
}

.project-quote {
    display: flex;
    width: 840px;
    border-radius: 16px;
    padding: 40px 36px;
    margin: 36px 12px 0 12px;
    p {
        font-size: 1.25rem;
        color: $text-color;
        line-height: 150%;
        padding: 0 24px;
    }
}

.quote-left {
    padding-bottom: 60px;
}
.quote-right {
    padding-top: 60px;
}

.project-fullimg {
    background-color: $project-bg-color;
    margin: 36px 0;
    padding: 32px 0;
    display: flex;
    justify-content: center;
    img {
        width: 960px;
    }
}

.project-medimg {
    display: flex;
    justify-content: center;
    padding: 36px 0;
}

.numbered-features {
    margin-top: 36px;
}

.big-numbers {
    font-size: 2.25rem;
    font-weight: 800;
}

.big-descriptions {
    font-size: 1.25rem;
    font-weight: 600;
}

.number-onleft {
    display: flex;
    gap: 16px;
    align-items: center;
}

.onleft-title {
    font-size: 1.25rem;
    font-weight: 600;
}

.feature-section {
    margin-bottom: 48px;
}

.thankyou {
    display: flex;
    height: 90px;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
    padding-top: 24px;
}